	'Â∞ÇÁî®„ÅÆËá™ÂÆ∂Ë£ΩÊ¥óÂâ§„Çí‰Ωø„Å£„Åü„É©„É≥„Éâ„É™„Éº„ÄÇ Ê±ö„Çå„ÅØÁßÅ„Åü„Å°„ÅÆÊ¥óÊµÑÂäõ„Å´ËÄê„Åà„Çã„Åì„Å®„Åå„Åß„Åç„Åæ„Åõ„Çì!',
	'Ëû∫Êóã‰∏∏„ÅÆÂäõ„ÅßÊ¥ó„ÅÜÔºÅ',
	'Â∏ØÈõª„Åó„ÅüÊ∞¥„ÅßÊ¥ó„ÅÑ„ÄÅ„Éê„ÇØ„ÉÜ„É™„Ç¢„ÇíÂèñ„ÇäÈô§„Åç„Åæ„Åô‚ö°‚ö°ÔºÅ',
	'„Ç∑„É≥„Éó„É´„ÄÇÈ´òÈÄü„ÄÇTran Xuan PhucÊßò„ÅÆÂ§ßÂ•Ω„Åç„Å™„É©„É≥„Éâ„É™„Éº„Ç∑„Éß„ÉÉ„Éó„ÄÇ',
